}

@media only screen and (max-width: 600px){
  .roadmap.desktop{
    display:none
  }
}

.roadmap.desktop .roadmap-item{
  padding-bottom:70px;
  padding-left:150px;
  position:relative
}

@media only screen and (max-width: 600px){
  .roadmap.desktop .roadmap-item{
    padding-left:100px;
    padding-bottom:130px
  }
}

.roadmap.desktop .roadmap-item:before{
  content:'';
  position:absolute;
  width:76px;
  height:90px;
  border-radius:50%;
  background:white;
  left:0;
  top:0
}

.roadmap.desktop .roadmap-item:after{
  content:'';
  position:absolute;
  left:35px;
  top:70px;
  height:100%;
  width:10px;
  background:white
}

.roadmap.desktop .roadmap-item:last-child:after{
  display:none
}

.roadmap.desktop .roadmap-item .roadmap-item-image{
  position:absolute;
  bottom:-65px;
  right:100px
}

@media only screen and (max-width: 600px){
  .roadmap.desktop .roadmap-item .roadmap-item-image{
    right:0px;
    bottom:45px
  }
}

.roadmap.desktop .roadmap-item .roadmap-item-image img{
  width:150px
}

@media only screen and (max-width: 600px){
  .roadmap.desktop .roadmap-item .roadmap-item-image img{
    width:100px
  }
}

.roadmap.mobile{
