  font-size:24px;
  text-decoration:none;
  position:relative;
  padding:10px;
  color:white
}

.roadmap.mobile .Collapsible .Collapsible__trigger:after{
  font-family:'FontAwesome';
  content:'\f107';
  position:absolute;
  right:10px;
  top:10px;
  display:block;
  transition:transform 300ms
